import { Outlet, Link } from "react-router-dom";

import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';

import Modal from 'react-bootstrap/Modal';

function ModalExample(props) {

    return (
        <>

        <Modal
            show={props.show}
            onHide={props.handleClose}
            backdrop="static"
            keyboard={false}
        >
            <Modal.Header closeButton>
                <Modal.Title>Modal title</Modal.Title>
            </Modal.Header>
            
            <Modal.Body>
                I will not close if you click outside me. Don't even try to press
                escape key.
            </Modal.Body>

            <Modal.Footer>
                <Button variant="secondary" onClick={props.handleClose}>
                    Close
                </Button>
                <Button variant="primary">Understood</Button>
            </Modal.Footer>

        </Modal>
        </>
    );
}

const Layout = () => {

    const [show, setShow] = useState(false);
    const [display_sideNav, setSideNav] = useState("");

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    const [toggled, setToggle] = useState("")

    const handleToggle = () => {
        
        if(toggled == "toggled"){
            setToggle("")
        }else{
            setToggle("toggled")
        }
        
    }

    const handleSideNav = () =>{

        if(display_sideNav == "d-none"){
            setSideNav("")
        }else{
            setSideNav("d-none")
        }
    }

    return (
        <>

            <div id="wrapper">
                <nav className={`navbar navbar-dark align-items-start sidebar sidebar-dark accordion bg-gradient-primary p-0 ${toggled} ${display_sideNav}` }>
                <div className="container-fluid d-flex flex-column p-0"><a className="navbar-brand d-flex justify-content-center align-items-center sidebar-brand m-0" href="#">
                    <div className="sidebar-brand-icon rotate-n-15"><i className="fas fa-bowling-ball" /></div>
                    <div className="sidebar-brand-text mx-3"><span>My Dashboard</span></div>
                    </a>
                    <hr className="sidebar-divider my-0" />
                    <ul id="accordionSidebar" className="navbar-nav text-light">
                        <li className="nav-item" />
                        <li className="nav-item"><Link className="nav-link active" to="/">Home</Link></li>
                        <li className="nav-item"><Link className="nav-link active" to="/blogs">Blogs</Link></li>
                        <li className="nav-item"><Link className="nav-link active" to="/contact">Contact</Link></li>
                        <li className="nav-item"><Link className="nav-link active" to="/dashboard">Dashboard</Link></li>
                    </ul>
                    <div className="text-center d-none d-md-inline">
                        <button id="sidebarToggle" className="btn rounded-circle border-0" type="button" onClick={handleToggle} />
                    </div>
                </div>
                </nav>
                <div id="content-wrapper" className="d-flex flex-column">
                
                    <div id="content">
                    
                        <nav className="navbar navbar-light navbar-expand bg-white shadow mb-4 topbar static-top">
                            <button className="btn btn-primary btn-sm d-none d-sm-inline-block ms-4" onClick={handleSideNav}>Menu</button>
                        </nav>

                        <Outlet />

                        <Button className="ms-4" variant="primary" onClick={handleShow}>
                            Launch static backdrop modal
                        </Button>

                    </div>

                    <footer className="bg-white sticky-footer">
                        <div className="container my-auto">
                        <div className="text-center my-auto copyright"><span>Copyright © Brand 2022</span></div>
                        </div>
                    </footer>
                </div>
                
                <a className="border rounded d-inline scroll-to-top" href="#page-top"><i className="fas fa-angle-up" /></a>
            </div>

            <ModalExample 
                show = {show}
                setShow = {setShow}
                handleClose = {handleClose}
                handleShow = {handleShow}
            />

            
        </>
    )
};

export default Layout;